@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@600;800&display=swap);
#slider-section {
    background-color: var(--whiteBackground);
    width: 100%;
    display: grid;
    place-items: center;
    border-bottom: 0.5px solid var(--grayText);
}
.slider{
    width: 100%;
    background: transparent;
    height: 12px;
    position: absolute;
    z-index: 4;
}
.slider::-moz-range-thumb {
    width: 34px;
    height: 34px;
    background: url(/interactive-pricing-component/static/media/icon-slider.cbca35e0.svg), var(--sliderBackground);
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 50%;
    border: none;
    cursor: pointer;
}
.slider::-webkit-slider-thumb {
    width: 20px;
    height: 20px;
    background: url(/interactive-pricing-component/static/media/icon-slider.cbca35e0.svg), var(--sliderBackground);
    background-repeat: no-repeat;
    background-position: center;
    border-radius: 50%;
    border: none;
    cursor: pointer;
}
.monthly-yearly{
    display: -webkit-flex;
    display: flex;
}
.pageviews{
    padding: 2rem 0;
}
.amount{
    color: var(--grayText);
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.amount strong{
    color: var(--CTAbackgroundandText);
    font-size: 2.5rem;
    font-weight: 800;
}
.slider-bar{
        width: 90%;
        position: relative;
        padding-bottom: 3rem;
}
.slider-full{
    position: absolute;
    height: 12px;
    background-color: var(--fullSliderBar) ;
    z-index: 3;
}
.slider-empty {
    position: absolute;
    height: 12px;
    background-color: var(--emptySliderBar);
    z-index: 2;
    width: 100%;
}
.monthly-yearly {
    font-size: 0.85rem;
    color: var(--grayText);
    padding: 3rem 0;
}
.choise-outside{
    padding: 0.5rem 1.3rem;
    height: 1rem;
    position: relative;
    background-color: var(--toggleBackground);
    border-radius: 1.5rem;
}
.inner-choise{
    position: absolute;
    background-color: var(--whiteBackground);
    width: 1rem;
    height: 0.8rem;
    border-radius: 50%;
    top: 1.5px;
  
}
.discount{
    max-height: 1rem;
    padding: 0 0.4rem;
    border-radius: 0.4rem;
    background-color: var(--discountBackground);
    color: var(--discountText);
}

@media screen and (min-width: 1000px) {
    #slider-section {
        position: relative;
    }
    .amount{
        position: absolute;
        right: 3rem;
        top: 1rem;
    }
    .pageviews{
        justify-self: left;
        margin-left: 3rem;
    }

    
}
.App {
    margin: 4rem auto;
    width: 90%;
    display: grid;
    place-items: center;
}
.main-header-text{
    text-align: center;
    font-size: 1.3rem;
     color: var(--CTAbackgroundandText);
     font-size: 800;
}
.intro{
    background: url(/interactive-pricing-component/static/media/pattern-circles.9f6ed14b.svg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    display: grid;
    place-items: center;
    padding-bottom: 1rem;
    margin-bottom: 2rem;
}
.main-descp{
    text-align: center;
    font-size: 0.9rem;
    max-width: 60%;
    margin: 1rem 0;
    color: var(--grayText);
}
.features{
    padding: 1.4rem 0;
    color: var(--grayText);
    width: 100%;
    display: grid;
    place-items: center;
}
.bottom-section {
    background-color: var(--whiteBackground);
    width: 100%;
    display: grid;
    place-items: center;
}
.feature{
    padding: 0.5rem 0;
}
.check-icon {
    padding: 0 0.6rem;
}
.start-trial{
    padding: 0.8rem 3rem;
    background-color: var(--CTAbackgroundandText);
    color: var(--CTAText);
    border-radius: 3rem;
}
@media screen and (min-width: 1000px) {
    .App {
        margin: 4rem auto;
        width: 43%;
    }
    .bottom-section{
        grid-template-columns: 1fr 1fr;
    }
    .features{
        place-items: start;
    }
    .check-icon {
        padding-left: 2rem;
    }
}
:root{
    --fullSliderBar: hsl(174, 77%, 80%);
    --sliderBackground: hsl(174, 86%, 45%);
    --discountBackground: hsl(14, 92%, 95%);
    --discountText:  hsl(15, 100%, 70%);
    --CTAText: hsl(226, 100%, 87%);
    --whiteBackground: hsl(0, 0%, 100%);
    --mainBackground: hsl(230, 100%, 99%);
    --emptySliderBar: hsl(224, 65%, 95%);
    --toggleBackground: hsl(223, 50%, 87%);
    --grayText: hsl(225, 20%, 60%);
    --CTAbackgroundandText: hsl(227, 35%, 25%);
    --fontFamily: 'Manrope';
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
        min-height: 100vh;
        font-family: 'Manrope';
        font-family: var(--fontFamily);
        background: url(/interactive-pricing-component/static/media/bg-pattern.76d3fa7d.svg);
        background-repeat: no-repeat;
        background-size: 100% 50%;

}
